function convertVttToDpe(webvtt) {
  const lines = webvtt.split('\n');

  let dpeTranscription = {
    paragraphs: [],
    words: []
  };

  lines.forEach((line, index) => {
    if (line.includes('-->')) {
      const timeMatch = line.match(/(\d{2}:\d{2}:\d{2}.\d{3}) --> (\d{2}:\d{2}:\d{2}.\d{3})/);
      const startTime = timeMatch ? convertTimeToSeconds(timeMatch[1]) : 0;
      const endTime = timeMatch ? convertTimeToSeconds(timeMatch[2]) : 0;
      const speakerMatch = lines[index + 1] && lines[index + 1].match(/<v ([a-zA-Z0-9]+)>/);
      const speaker = speakerMatch ? speakerMatch[1] : 'Add speaker';
      const content = lines[index + 1] && lines[index + 1].replace(/<[^>]*>/g, '').trim();  // Remove HTML tags and trailing spaces
    
      dpeTranscription.paragraphs.push({start: startTime, end: endTime, speaker: speaker});
      // Split content into words and add to the words array
      const wordsArray = content && content.split(' ');
      const wordDuration = (endTime - startTime) / (wordsArray ? wordsArray.length : 1);
      const words = wordsArray && wordsArray.map((word, wordIndex) => ({
          start: startTime + (wordIndex * wordDuration),
          end: startTime + ((wordIndex + 1) * wordDuration),
          text: word,
          speaker
      }));

      dpeTranscription.words.push(...(words || []));
    }
  });

  function convertTimeToSeconds(time) {
    const [hours, minutes, seconds] = time.split(':').map(Number);
    return hours * 3600 + minutes * 60 + parseFloat(seconds);
  }

  return dpeTranscription;
}

export { convertVttToDpe };